body {
  background-color: #2c3e50;
  color: #ecf0f1;
  font-family: Arial, sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px;
}

.btn {
  background-color: #3498db;
  border: none;
  color: #ecf0f1;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 5px;
}

.input {
  width: 60%;
  height: 150px;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  background-color: #34495e;
  color: #ecf0f1;
  font-size: 16px;
  border-radius: 5px;
}

.message {
  font-size: 18px;
}

.success {
  color: #2ecc71;
}

.failure {
  color: #e74c3c;
}

